.catalog-wrapper{max-width:845px}.catalog-wrapper .card{border-radius:20px;box-shadow:0px 0px 30px 0px rgba(0,32,50,.2);background:#fff;width:255px;height:348px;border:none}.catalog-wrapper .card .card-body{padding-top:20px;padding-bottom:22px}.catalog-wrapper .card .card-body .card-title{color:#002032;font-family:Inter Tight;font-size:20px;font-weight:400;line-height:100%;letter-spacing:0%;text-align:left;margin-bottom:7px}.catalog-wrapper .card .card-body .card-text{color:#00a2ff;font-family:Inter Tight;font-size:20px;font-weight:700;line-height:100%;letter-spacing:0%;text-align:left;margin-bottom:32px}.catalog-wrapper .card .card-body .btn{width:212px;height:38px;margin:0 auto;display:flex;flex-direction:row;justify-content:center;align-items:center;margin-top:auto;border-radius:8px;border:none;background:#00a2ff;transition:all .3s ease-in-out;color:#fff;font-family:Inter Tight;font-size:14px;font-weight:500;line-height:100%;letter-spacing:.5px;text-align:center}.catalog-wrapper .g-6{--bs-gutter-y: 40px;--bs-gutter-x: 60px}@media(max-width: 1400px){.section-catalog .container .catalog-container .catalog-wrapper .g-6{--bs-gutter-y: 30px;--bs-gutter-x: 20px}}